body{
  font-family: 'Dosis', sans-serif;
  background-image: linear-gradient(to bottom right,white,#c5aff8);
  background-repeat: no-repeat;

}

.btn-color{
  background-color: #9f7fec;
}
.img-tran{
  transform: none;
  transition: transform 2s;
  transition-timing-function: ease-out
}
.img-tran:hover{
transform: rotateX(160deg);
}

.my-icon{
  color: #9f7fec;
  text-align: none;
  animation-name: example;
  animation-duration: 3s;
  text-align: center;
  animation-iteration-count: infinite;
}
@keyframes example {
from{color: #9f7fec;}
to{color: black;}
}
.img-tran2{
  transform: none;
  transition: transform 2s;
  transition-timing-function: ease-out
}
.img-tran2:hover{
  transform: rotateY(160deg);
  }
  .text-c{
    text-align: center;
  }
  .text-size-card{
    font-size: 20px;
  }
  #displayset{
   background-color: none;
  }
  #createvis{
    visibility: hidden;
  }
  .img-width{
    width: 25rem;
  }